idbtnkiemtra.addEventListener("click", (e) => {
  e.preventDefault();
  var resultEle = document.getElementById("result");
  var name = idmatracuu.value;
  var date = idngaytracuu.value;
  var idate = parseInt(date.split("-").join(""));
  if (name === "") {
    alert("Vui l√≤ng nh·∫≠p m√£ nh√¢n vi√™n tra c·ª©u");
    return;
  }

  let submitData = {
    type: "check",
    data: { name, idate }
  };
  modal.classList.add("display");
  console.log(submitData);
  fetch(URL, {
    method: "POST",
    headers: {
      "Content-Type": "text/plain;charset=utf-8"
    },
    body: JSON.stringify(submitData) // p data type must match "Content-Type" header
  })
    .then((response) => {
      return response.json();
    })
    .then((data) => {
      console.log(data);
      modal.classList.remove("display");
      render(data);
    })
    .catch((error) => {
      console.error("Error:", error);
      modal.classList.remove("display");
      resultEle.innerHTML = `
      <div class="koduyet" style="color:red">
      <p>Kh√¥ng c√≥ k·∫øt qu·∫£ n√†o ph√π h·ª£p!</p>
      </div>
      `;
      // alert("kh√¥ng c√≥ k·∫øt qu·∫£ n√†o, h√£y ki·ªÉm tra th√¥ng tin tra c·ª©u v√† th·ª≠ l·∫°i");
    });
});

